let ResultComment = [
  {
    minScore: 0,
    maxScore: 3,
    commentText: '你能控制你的健身計劃。你現在並無過度健身的危機。如大部分人般，你有時享受你的健身計劃，有時不。重新評估健身的動力來源，或許有助你達到理想的體型。'
  },
  {
    minScore: 4,
    maxScore: 6,
    commentText: '你處於過度健身的邊緣。你需要重新評估你的健身行為如何影響到你生活的其他部分。就算你認為沒有影響到其他生活部分，你的答案也反映了你對現時的健身方式並不享受。'
  },
  {
    minScore: 7,
    maxScore: 14,
    commentText: '你依賴健身來肯定自我價值。這種依賴對你生活的其他部分造成問題，或許你會感到被健身控制你的生活。請記住，均衡的運動及飲食，才能令你有健康的人生。'
  }
]

export default ResultComment



